"use client";
import { useState } from "react";

export default function LoginPage() {
  const [isRegister, setIsRegister] = useState(false);
  const [nome, setNome] = useState("");
  const [senha, setSenha] = useState("");
  const [message, setMessage] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage("");

    const endpoint = isRegister
      ? "https://localhost:7203/api/usuarios/criar"
      : "https://localhost:7203/api/usuarios/login";

    const payload = { nome, senha };

    try {
      const response = await fetch(endpoint, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await response.json();

      if (!response.ok) {
        setMessage(data || "Erro ao processar a solicitação.");
        return;
      }

      if (isRegister) {
        setMessage("Usuário cadastrado com sucesso! Faça login.");
        setIsRegister(false);
      } else {
        localStorage.setItem("token", data.token);
        setMessage("Login realizado com sucesso!");
      }
    } catch (error) {
      setMessage("Erro ao conectar ao servidor.");
    }
  };

  return (
    <div
      className="flex items-center justify-center h-screen bg-cover bg-center"
      style={{ backgroundImage: "url('/background_login.jpg')" }}
    >
      <div className="relative p-8 rounded-lg shadow-lg w-96 text-center">
        <div className="absolute inset-0 bg-white/40 backdrop-blur-lg rounded-lg"></div>

        <div className="relative z-10">
          <h2 className="text-2xl font-bold text-blue-600 mb-6">
            {isRegister ? "Cadastro" : "Login"}
          </h2>
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label className="block text-gray-700 text-left mb-1">Nome</label>
              <input
                type="text"
                value={nome}
                onChange={(e) => setNome(e.target.value)}
                className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div className="mb-4">
              <label className="block text-gray-700 text-left mb-1">Senha</label>
              <input
                type="password"
                value={senha}
                onChange={(e) => setSenha(e.target.value)}
                className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <button
              type="submit"
              className="w-full bg-blue-600 text-white p-2 rounded hover:bg-blue-700 transition"
            >
              {isRegister ? "Cadastrar" : "Login"}
            </button>
          </form>
          {message && <p className="mt-4 text-red-600 text-sm">{message}</p>}
          <p className="mt-4 text-gray-600 text-sm">
            {isRegister ? "Já tem uma conta?" : "Não tem uma conta?"}{" "}
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                setIsRegister(!isRegister);
              }}
              className="text-blue-500 hover:underline"
            >
              {isRegister ? "Faça login" : "Cadastre-se"}
            </a>
          </p>
        </div>
      </div>
    </div>
  );
}
